@import "../../../sass/helpers/mixins";
@import "../../../sass/variables/colors";
@import "../../../sass/helpers/keyframes";

.item {
  @include flex-layout(column, initial, initial, 7px);
  user-select: none;
  position: relative;
}

.item__image {
  position: relative;
  border-radius: 10px;
  overflow: hidden;
  &:hover {
    & .item__image__overlay {
      opacity: 1;
    }
  }
  img {
    display: block;
    border-radius: 10px;
    transition: 0.3s;
    width: 100%;
    object-fit: cover;
  }
}

.item__bookmark {
  position: absolute;
  top: 8px;
  right: 8px;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.3411764706);
  cursor: pointer;
  z-index: 10;
  width: 35px;
  aspect-ratio: 1/1;
  display: grid;
  place-items: center;
  font-size: 18px;
  color: white;

  ion-icon[name="bookmark"] {
    animation: scale 0.8s cubic-bezier(0.08, 0.99, 0.36, 1.2);
  }
}

.item__details {
  @include flex-layout(row, initial, center, 7px);
  > * {
    color: rgb(145 145 144);
    font-size: 13px;
  }
}

.trend_item_description_holder {
  position: absolute;
  bottom: 8px;
  left: 8px;
}

.item__name__small {
  color: #fff;
  font-size: 1rem;
}
.item__name__big {
  color: #fff;
  font-size: clamp(1rem, 4vmin, 1.4rem);
}

.item__image__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  transition: 0.3s;
  background: rgba(0, 0, 0, 0.247);
  @include flex-layout(row, center, center, null);
  opacity: 0;

  &:hover ~ img {
    transform: scale(1.2) rotate(-6deg);
  }
}
.overlay__content {
  @include flex-layout(row, center, center, 10px);
  padding: 10px 22px;
  border-radius: 20px;
  color: white;
  background: rgba(255, 255, 255, 0.216);
}
