@import "/sass/helpers/mixins";
@import "/sass/variables/colors";

.label {
  @include flex-layout(row, center, center, 15px);
  margin-top: 10px;
  margin-left: 5px;

  ion-icon {
    font-size: 1.8rem;
    color: #fff;
  }

  input {
    font-size: 19px;
    padding: 14px 0;
    border: none;
    border-bottom: 1px solid transparent;
    outline: none;
    background: none;
    flex-grow: 1;
    color: white;

    &:focus {
      border-bottom: 1px solid white;
    }

    &::placeholder {
      color: rgb(90, 105, 143);
    }
  }
}
