.container {
  padding-top: 40px;
}
.image {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: rgba(0, 0, 0, 0.2);
  }
  img {
    max-height: 50vh;
    width: 100%;
    object-fit: cover;
    display: block;
  }
}

.details {
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-top: 20px;
  color: #fff;

  h2 {
    margin: 0;
    font-size: clamp(1.4rem, 7vmin, 2.2rem);
    font-weight: 500;
  }
}

.inner-flex {
  display: flex;
  gap: 16px;
  color: #777;
}

.category,
.year,
.rating {
  font-size: clamp(15px, 4vmin, 1rem);
}

.description {
  line-height: 1.6;
  color: #eee;
}
