@import "../../../../sass/helpers/mixins";
$blue: #0075ff;

/**
  * Dots
  *
  * @author jh3y - jheytompkins.com

*/
.container {
  @include flex-layout(row, center, center, null);
  min-height: 60vh;
}

.dots:before {
  display: block;
  height: 5px;
  width: 5px;
  content: "";
  -webkit-animation: spin 1s infinite;
  animation: spin 1s infinite;
  border-radius: 100%;
  -webkit-box-shadow: 20px 0 0 5px $blue, -20px 0 0 5px $blue;
  box-shadow: 20px 0 0 5px $blue, -20px 0 0 5px $blue;
}

@-webkit-keyframes spin {
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes spin {
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
