@import "../../../../sass/helpers/mixins";
@import "../../../../sass/variables/colors";

.profile {
  @include flex-layout(row, center, center, 30px);
  margin-top: 70px;
}

.flex {
  padding: 35px 25px;
  border-radius: 15px;
  background: $lightGrey;

  &.flex-left {
    @include flex-layout(column, null, null, 6px);
    input,
    textarea {
      &::placeholder {
        color: rgb(150, 147, 147);
      }
    }

    &.editing {
      input,
      textarea {
        // border: 1px solid rgba(0, 0, 0, 0.1);
        pointer-events: initial;
      }
    }

    input,
    textarea {
      width: 100%;
      border: none;
      outline: none;
      background: none;
      padding: 5px;
      border: 1px solid transparent;
      pointer-events: none;
    }

    .name-input {
      margin-top: 9px;
      font-size: 1.7rem;
      color: hsl(214deg 50% 23%);
    }

    textarea {
      overflow: hidden;
      background: none;
      margin: 9px auto;
      max-width: 500px;
      line-height: 1.5;
      color: #cfc9c9;
      resize: none;
    }
  }
}

.pp {
  width: fit-content;
  border-radius: 50%;
  position: relative;
  input {
    appearance: none;
    display: none;
  }
  img {
    width: 150px;
    border-radius: 50%;
    cursor: pointer;
    aspect-ratio: 1/1;
    object-fit: cover;
    background-position: top;
    display: block;
  }
}

.disabled {
  pointer-events: none;
}

.title {
  color: hsl(214deg 50% 23%);
  font-size: 1.1rem;
}

.error {
  color: #fc4747;
}
.success {
  color: #10dc60;
}
