@import "../../../sass/helpers/mixins";
@import "../../../sass/helpers/keyframes";
@import "../../../sass/variables/colors";

.profile {
  @include flex-layout(column, center, center, 10px);

  ul {
    animation: fadin 0.5s linear;
    padding: 16px 25px;
    position: absolute;
    right: -170px;
    bottom: 10px;
    box-shadow: 0 0 0 1.5px #0075ff;
    border-radius: 7px;
    background: $lightBlack;

    @include breakpoint-max(small) {
      right: 16px;
      bottom: -156px;
    }
    li {
      color: $text;
      padding: 5px 0;
      font-size: 14px;
      cursor: pointer;
      &:first-child {
        color: #fff;
      }
      &:last-child {
        color: $red;
        cursor: pointer;
      }
    }
  }
}

.profile__image {
  cursor: pointer;
  background-size: cover !important;
  background: #777;
  border-radius: 50%;
  aspect-ratio: 1/1;
  user-select: none;
  object-fit: cover;
  min-width: 36px;
  width: 40px;
}
