@import "../../../sass/helpers/mixins";
@import "../../../sass/variables/colors";

.header {
  flex: 100%;
  position: fixed;
  z-index: 1000;

  @include breakpoint-min(small) {
    height: 91vh;
    top: 50%;
    left: 15px;
    transform: translateY(-50%);
    width: fit-content;
  }
  @include breakpoint-max(small) {
    top: 20px;
    width: 97%;
    left: 50%;
    transform: translateX(-50%);
  }

  .container {
    height: 100%;
    padding: 15px;
    background: $lightBlack;
    border-radius: 5px;
    @include flex-layout(row, space-between, center, null);
    @include breakpoint-min(small) {
      flex-direction: column;
    }

    .logo {
      flex: 1;
    }

    .nav {
      flex: 2;
      @include flex-layout(row, space-between, center, null);
      @include breakpoint-min(small) {
        flex-direction: column;
        height: 100%;
      }

      .menu {
        @include flex-layout(row, space-between, center, null);
        @include breakpoint-min(small) {
          flex-direction: column;
          gap: 20px;
        }
      }
    }
  }
}

.frontend-mentor {
  text-align: center;
  position: relative;
  left: 50%;
  color: #fff;
  display: flex;
  align-items: center;
  gap: 4px;
  width: fit-content;
  margin-bottom: 30px;
}
