@import "../sass/helpers/mixins";

@mixin breakpoint-max($screen) {
  $size: 0px;

  @if $screen == mobile {
    $size: 767px;
  } @else if $screen == small {
    $size: 991px;
  } @else if $screen == medium {
    $size: 1199px;
  }

  @media (max-width: $size) {
    @content;
  }
}

.wrapper {
  padding-top: 95px;
  padding-bottom: 40px;
  height: 100%;
  display: flex;
  overflow-x: hidden;

  @include breakpoint-min(small) {
    gap: 80px;
  }
}

.flex-right {
  flex-grow: 1;
  @include flex-layout(column, initial, initial, null);
}

.frontend-mentor {
  display: inline-flex;
  text-align: center;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  color: #fff;
  display: flex;
  align-items: center;
  gap: 10px;
  width: fit-content;
  margin: 20px 0;
  img {
    display: block;
  }
}
