@import "../../../sass/helpers/mixins";
@import "../../../sass/variables/colors";

.actions {
  position: relative;

  &.show {
    .actions__menu {
      height: 67px;
    }
  }

  font-size: 26px;
  color: $text;
  cursor: pointer;
  display: grid;
  place-items: center;

  .actions__menu {
    background: $text;
    border-radius: 5px;
    text-align: center;
    order: -1;
    transition: 0.5s;
    height: 0;
    overflow: hidden;
    @include flex-layout(column, center, center, 10px);
    position: absolute;
    top: 100%;
    left: -40px;
    width: 60px;

    @include breakpoint-min(small) {
      left: 120%;
      top: -20px;
    }

    li {
      font-size: 14px;
      > * {
        color: $white;
      }
    }
  }
}
